.fileupload-cs-alert-container { 
    text-align: left;
    vertical-align: middle;
    border-radius: 6px;   
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: left;
    justify-content: left;
    font-size: 14px;
    margin-top: 20px;
    padding: 10px 0;
}

.fileupload-cs-alert-warning{
    background-color: #fcf3e7;
    border: 1px solid #e49034;
    color: #425563;
}

.fileupload-cs-alert-error{
    background-color: rgba(221,37,9,.1);
    border: 1px solid #dd2509;
    color: #425563;
}

.fileupload-cs-alert-success{
    background-color: #d4edda;
    border: 1px solid #c3e6cb;;
    color:#425563;
}

.fileupload-cs-alert-info{

    background-color: #f5fbfd;
    border: 1px solid #005eb8;
    color:#425563;
}

.fileupload-cs-alert-container ul.message-list{
    margin-bottom: 0;
    margin-top:0;
    list-style: none;
    padding-left: 10px;
}

.fileupload-cs-alert-container li.message-line{
    background-position: 0 0;
    padding-left: 30px;
    min-height: 22px;
   
}

.fileupload-cs-alert-container li.message-bullet{
    background-position: 0 0;
    padding-left: 30px;
    min-height: 22px;
    margin-left: 60px;
    list-style: inside;
}

.fileupload-cs-message-line-warning{
    background:url(./images/alert-icon-warning.svg) left center no-repeat;;
}

.fileupload-cs-message-line-error{
    background:url(./images/alert-icon-error.svg) left center no-repeat;;
}

.fileupload-cs-message-line-info{
    background:url(./images/alert-icon-info.svg) left center no-repeat;;
}

.fileupload-cs-message-line-success{
    background:url(./images/alert-icon-info.svg) left center no-repeat;;
}
